import React, {Component} from 'react';
import Button from '../components/Button';
import Fact from '../components/Fact';

class NumberMagic extends Component {

	constructor(){
		super();

		this.state = {
			input:'',
			story:''
		}
	}

	onInputChange=(event)=>{
		this.setState({input:event.target.value})
	}

	onSubmit=()=>{
		const str = this.state.input;
		if(str.length){
			fetch(`https://cors-anywhere.herokuapp.com/http://numbersapi.com/${this.state.input}`)
			.then(res => res.text())
			.then(res => this.setState({story:res}));
		}
		
	}

	render(){
		return(
			<div className="story">
				<h1 className="story__heading">Each Number has a story. Want to know ?</h1>
				<p className="story__para">You have to enter a number or a date in month/day(e.g 5/11) formate</p>
				<input className="story__input" type="text" placeholder="Number or Date(e.g 5/11)" onChange={this.onInputChange} />
				<Button click={this.onSubmit} str={'Submit'} x={0}/>
				<Fact story={this.state.story}/>
			</div>
		);
	}
}

export default NumberMagic;