import React, {Component} from 'react';
import Button from '../components/Button';

class Space extends Component {

	constructor(){
		super();

		this.state = {
			latitude:0,
			longitude:0
		}
	}

	onRefresh=()=>{
		fetch('https://cors-anywhere.herokuapp.com/http://api.open-notify.org/iss-now.json')
		.then(res => res.json())
		.then(res => {
			this.setState({latitude : res.iss_position.latitude,
						longitude: res.iss_position.longitude
			})
		});
	}

	render(){
		return(
			<div className="space">
				<h1 className="space__header">International Space Station Current Location</h1>

				<p className="space__para">The International Space Station is moving at close to 28,000 km/h 
				so its location changes really fast! 
				So press the below button to see the current position.</p>

				<div className="space__img-box">
					<img className="space__img" alt="space station" src="https://wallpaper-mania.com/wp-content/uploads/2018/09/High_resolution_wallpaper_background_ID_77701921822.jpg" />
				</div>

				<div className="space__result">
					<Button click={this.onRefresh} str={'locate'} x={1}/>
					<p>Latitude: {this.state.latitude}</p>
					<p>Longitude: {this.state.longitude}</p>
				</div>
			</div>
		);
	}
}

export default Space;